@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
@font-face {
  font-family: AppleSDGothicNeo;
  src: url(/mulmak-noodles-front/static/media/AppleSDGothicNeoB.4175a777.ttf);
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  max-width: 510px;
  min-height: 100vh;
  padding: 30px;
  background: #f8efef;
  margin-left: auto;
  margin-right: auto;
}

.header {
  font-size: 24px;
}

a{
  text-decoration: none
}

.saved-child{
    width: 368px;
    margin: 30px auto;
    /* padding: 17px 230px 17px 11px; */
    padding : 10px;
    border-radius: 10px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #ffffff;
}

.saved-child h3{
    margin: 10px 0;
    opacity: 0.7;
    font-family: AvenirNext;
    font-size: 14px;
    font-weight: 900;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.09px;
    text-align: left;
    color: #000000;
}

.saved-child .container{
    display: flex;
    justify-content: space-between;
}

.saved-child h3:last-child, .saved-child .account{
    opacity:0.5
}

.register *{
    box-sizing: border-box
}

.register .child-info label {
    /* width: 60px; */
    width:100%;
    height: 19px;
    margin: 38px 151.3px 13px 0.3px;
    font-family: SpoqaHanSans;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.1px;
    text-align: left;
    color: #7e84a3;
} 


.register .child-info .types input, .register .child-info .types select{
    /* width: 368px; */
    width:100%;
    height: 44px;
    margin: 13px 0.5px 24px 0.3px;
    padding-left: 10px;
    border-radius: 4px;
    border: none;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #ffffff;
}

.register .child-info .types input:focus{
    outline: none;
}

/* Pay Info */

.register .pay-info > h3 {
    height: 19px;
    margin: 24px 123.3px 28px 0.3px;
    font-family: SpoqaHanSans;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.1px;
    text-align: left;
    color: #7e84a3;
}


.register .pay-info .flex-container{
    display: flex;
    justify-content : space-between
}
.register .pay-info .types label {
    height: 15px;
    margin: 30px 0px 8px 0px;
    font-family: SpoqaHanSans;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.08px;
    text-align: left;
    color: #7e84a3;
}

.register .pay-info .types input {
    width:310px;
    margin: 8px 0px 8.3px 8px;
    padding-bottom: 10px;
    border : none;
    border-bottom: 0.5px solid #707070;
    background: transparent;
    font-family: SpoqaHanSans;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.08px;
    text-align: left;
}

.register .pay-info .types input:focus{
    outline: none;
}

/* Button */

.register .register-button{
    display: flex;
}

.register .register-button button{
    width: 368px;
    height: 60px;
    margin:100px auto;
    padding: 18px 111px;
    border-radius: 10px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #ffffff;    
    border:none;
    font-family: SpoqaHanSans;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.09px;
    text-align: left;
    color: #7e84a3;
}

.register .register-button button:active{
    background:#ddd;
}
.useage-history *{
    box-sizing: border-box;
}


/* Calendar */

.useage-history .calendar{
    margin : 30px 0;
    display : flex
}

.useage-history .calendar .react-calendar {
    margin : auto;
    border : none;
    border-radius : 10px
}

.react-calendar__month-view__days__day--weekend{
    color:black
}
.react-calendar__month-view__days__day--neighboringMonth{
    color:gray
}

.react-calendar__tile--now{
    background-color:white
}
